

























































































































































































































































.select-page {
  font-size: 14px;
  color: #777;
}

.info-block {
  position: relative;

  .movie-info {
    padding: 40px;
    display: flex;

    .flex {
      flex: 1;

      .title {
        width: 80vw;
        height: 70px;
        line-height: 60px;
        font-size: 40px;
        font-weight: 700;
        color: #333;
      }

      .info {
        font-size: 24px;
        padding-top: 5px;
        line-height: 1;
      }
    }
  }
}

.select-block {
  background-color: #f2f1f6;
  margin-top: 20px;
  height: 50vh;
  position: relative;
  overflow: hidden;
  .hall-name {
    display: inline-block;
    width: 400px;
    height: 40px;
    background: #e3e3e3;
    border-radius: 0 0 30px 30px;
    z-index: 99;
    text-align: center;
  }

  .row-nav {
    z-index: 2;
    width: 50px;
    font-size: 14px;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.05);
    pointer-events: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    div {
      height: 40px;
      line-height: 40px;
      padding-top: 5px;
    }
  }

  .seats-block {
    margin-left: 60px;

    .seats-wrap {
      width: 380px;

      .wrap {
        display: -webkit-box;

        .seat {
          padding: 0 15px;
          height: 40px;
          width: 40px;
          box-sizing: border-box;
          margin-left: 10px;
          margin-top: 10px;
        }

        .bgImg {
          background-image: url("https://p1.meituan.net/movie/9dfff6fd525a7119d44e5734ab0e9fb41244.png");
          background-size: 100% 100%;
        }

        .bgChcked {
          background-image: url("https://p1.meituan.net/movie/bdb0531259ae1188b9398520f9692cbd1249.png");
          background-size: 100% 100%;
        }

        .bgUnchecked {
          background-image: url("https://p0.meituan.net/movie/585588bd86828ed54eed828dcb89bfdd1401.png");
          background-size: 100% 100%;
        }
      }
    }

    .type-seat {
      display: flex;
      justify-content: center;
      position: absolute;
      bottom: 30px;
      width: 500px;
      .type {
        display: flex;
        margin-left: 15px;
        img {
          height: 40px;
          width: 40px;
        }
        .name {
          line-height: 50px;
          padding-left: 10px;
        }
      }
    }
  }
}

.smart-choose-wrapper {
  display: flex;
  padding: 30px;

  .title {
    line-height: 65px;
  }

  .recommend-seat {
    display: flex;

    div {
      line-height: 65px;
      border: 1px solid #d7d7d7;
      border-radius: 4px;
      font-size: 22px;
      margin-left: 14px;
      text-align: center;
      box-sizing: border-box;
      width: 100px;
      height: 60px;
    }
  }
}

.price-block {
  padding: 30px;
  align-items: center;

  .ticket-block {
    display: flex;
    flex-wrap: wrap;

    .select-seats-item {
      text-align: center;
      margin-right: 12px;
      margin-top: 10px;
      font-size: 18px;
      padding: 4px 20px;
      border: 1px solid #ef4238;
      width: 150px;
      box-sizing: border-box;
    }
  }
}

.submit-block {
  padding: 20px;

  .submit {
    line-height: 80px;
    font-size: 30px;
    font-weight: 700;
    text-align: center;
    color: #fff;
    border-radius: 10px;
    background-color: #f90 !important;
    width: 90vw;
  }

  .disabled {
    background-color: #ffddb2 !important;
  }
}
